/* Buttons
/* ---------------------------------------------------------- */

.gh-button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    min-width: 150px;
    min-height: 60px;
    height: var(--height);
    padding: 0 2rem;
    border: 0;
    cursor: pointer;
    font-family: var(--font-title);
    font-size: 1.6rem;
    font-weight: var(--font-normal);
    line-height: var(--height);
    text-align: center;
    text-decoration: none;
    white-space: nowrap;
    appearance: none;
    transition: 0.4s ease;
}

.gh-button.gh-button-fit {
    width: 100%;
}

.gh-button.gh-button-small {
    height: calc(var(--height) * 0.9);
    line-height: calc(var(--height) * 0.9);
    padding: 0 1.5rem;
    font-size: 1.2rem;
}

.gh-button.gh-button-large {
    height: calc(var(--height) * 1.2);
    line-height: calc(var(--height) * 1.2);
    padding: 0 2.5rem;
    font-size: 1.7rem;
}

.gh-button.gh-button-disabled,
.gh-button:disabled {
    pointer-events: none;
    opacity: 0.4;
}

.gh-button {
    color: var(--color-base);
    background-color: var(--color-button-main);
}


.gh-button:hover {
    text-decoration: none;
    transition: 0.2s ease;
}


.gh-button.gh-button-primary { /* todo adjust colors? */
    color: #fff !important;
    background-color: var(--color-primary);
    box-shadow: none;
}

.gh-button.gh-button-primary:hover { /* todo adjust colors? */
    background-color: var(--color-primary);
}
